import React from 'react';
import './_resume.scss';

import { StaticQuery, graphql } from 'gatsby';

const Resume = () => (
    <StaticQuery
        query={ graphql`
      query ResumeQuery {
        allDataJson {
          edges {
            node {
              resume {
                title
                role
                date
              }
            }
          }
        }
      }
    ` }
        render={ (data) => (
            <section className="resume" id="resume">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                          <div class="card card-tertiary">
                              <div class="card-header">
                                <div className="row">
                                  <div className="col-12">
                                    <h2 className="resume-title">Resume</h2>
                                  </div>
                                </div>
                              </div>
                              <div className="row">
                                <div className="col-12">
                                  <div class="card-body resume-list">
                                    <ul>
                                      {data.allDataJson.edges[0].node.resume.map((item, i) => (
                                          <li className="resume-list-item" key={ i }>
                                              {item.title}
                                              <ul>
                                                <li>{item.date}</li>
                                                <li>{item.role}</li>
                                              </ul>
                                          </li>
                                      ))}
                                      </ul>
                                  </div>
                                </div>
                              </div>
                          </div>
                        </div>
                    </div>
                </div>
            </section>
        ) } />
);

export default Resume;
