import React from 'react';

import './_interests.scss';

import { StaticQuery, graphql } from 'gatsby';

const Interests = () => (
    <StaticQuery
        query={ graphql`
      query InterestsQuery {
        allDataJson {
          edges {
            node {
              interests {
                title
              }
            }
          }
        }
      }
    ` }
        render={ (data) => (
          <section className="interests" id="interests">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                      <div class="card card-tertiary">
                          <div class="card-header">
                            <div className="row">
                              <div className="col-12">
                                <h2 className="interests-title">Interests</h2>
                              </div>
                            </div>
                          </div>
                          <div className="row">
                            <div className="col-12">
                              <div class="card-body interests-list">
                                <ul>
                                    {data.allDataJson.edges[0].node.interests.map((item, i) => (
                                        <li className="interests-list-item" key={ i }>
                                            {item.title}
                                        </li>
                                    ))}
                                    <li className="interests-list-item win95-link" key={"win95-link"}>
                                      Please go here: https://demo.themesberg.com/windows-95-ui-kit/
                                    </li>
                                  </ul>
                              </div>
                            </div>
                          </div>
                      </div>
                    </div>
                </div>
            </div>
          </section>


        ) } />
);

export default Interests;
